import { configureStore, createSlice } from "@reduxjs/toolkit";

let user = createSlice({
  name: "user",
  initialState: { name: "박지윤", age: 20 },

  reducers: {
    changeName(state) {
      // array/object 경우 직접 수정해도 수정이 된다.
      // return { name: "park", age: 20 };
      state.name = "park";
    },
    changeNum(state, action) {
      state.age += action.payload;
    },
    // payload : changeNum 함수를 실행할때 payload(화물) 화물도 같이 보낸다는 것처럼
    // 파라미트 작명할때 action이라 보통 한다.
    // 화물뿐만아니라 action에 대한 여러정보도 있기때문.  (state변경함수를 통틀어 action이라 함)
  },
});

export let { changeName, changeNum } = user.actions;

export default user;
